/* App.css */
.app {
  font-family: Arial, sans-serif;
  text-align: center;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #282c34;
  color: white;
}

.header h1 {
  margin: 0;
}

.header nav {
  display: flex;
  gap: 20px;
}

.header nav button {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 1em;
}

.header nav button:hover {
  text-decoration: underline;
}

.footer {
  padding: 20px;
  background-color: #282c34;
  color: white;
}

.footer .social-links {
  display: flex;
  gap: 10px;
}

.home, .education, .experience, .projects, .skills {
  padding: 20px;
}

.education ul, .experience ul, .projects ul, .skills ul {
  list-style-type: none;
  padding: 0;
}

.job, .project {
  margin-bottom: 20px;
}
